.movie-preview {
    display: flex;
    flex-direction: column;
    align-self: center;
    gap: 10px;

    width: 300px;
    box-shadow: $shadow;

    .img-container {
        height: 150px;

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    h3 {
        text-align: center;
        color: $brand;

        font-size: 16px;
        font-weight: 600;
    }

    p {
        text-align: center;
        color: $txt;

        font-size: 14px;
        padding: 0 15px 15px;
    }
}

@media (min-width: 461px) {

    .movie-preview {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 15px;
        align-self: flex-start;

        width: 300px;
        margin-bottom: 75px;
        box-shadow: $shadow;
        cursor: pointer;

        &:hover {
            margin-bottom: 0;

            h3 {
                margin-bottom: 0px;
            }

            p {
                display: block;
                text-align: center;
                color: $txt;

                font-size: 14px;
                padding: 0 15px 15px;
            }
        }

        .img-container {
            height: 150px;

            img {
                width: 100%;
                object-fit: cover;
            }
        }

        h3 {
            text-align: center;
            color: $brand;

            font-size: 18px;
            font-weight: 600;
            margin-bottom: 15px;
        }

        .short-des {
            display: none;
        }
    }
}
