* {
    box-sizing: border-box;
}

html {
    font-family: os, sans-serif;
}

body {
    margin: 0;
    background-color: $bg;
    color: $txt;
    direction: rtl;
    overflow-x: hidden;
    width: 100vw;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
}

a {
    color: $txt;
    text-decoration: none;

    &.active {
        color: $brand;
    }
}

button {
    cursor: pointer;
    border-radius: 7px;
    border: none;
    font-family: os, sans-serif;
}

img {
    height: 100%;
    object-fit: cover;
}

input {
    border: none;
    outline: none;
    font-family: roboto, sans-serif;
    margin: 0;
    cursor: pointer;
}

hr {
    margin: 25px;
    border: 0;
    border-bottom: 1px solid $txt;
}

::-webkit-scrollbar {
    width: 16px;
    border-radius: 10px;
}

::-webkit-scrollbar-track {
    background: rgba(128, 128, 128, 0.231);
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: $tool;
    border-radius: 10px;
}