.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100vw;

    header {
        display: flex;
        flex-direction: column;
        height: fit-content;

        .img-container {
            height: 55px;
            margin-top: 55px;
            margin-bottom: 25px;

            img {
                height: 100%;
                object-fit: contain;
            }
        }

        .headline {
            width: 100%;
            text-align: center;
            margin-bottom: 30px;

            h1 {
                font-size: 30px;
                color: $brand;
                margin: 0 auto 15px;
                font-family: os-bold, sans-serif;
            }

            h2 {
                font-size: 22px;
                color: $white;
                font-family: os-light, sans-serif;
                font-weight: 100;
                max-width: 20ch;
                margin: 0 auto 15px;
                line-height: 1.5;

                span {
                    font-family: os-bold, sans-serif;
                    color: $brand;
                    letter-spacing: 1px;
                    margin-inline-end: 7px;
                }
            }
        }
    }

    .info {
        // width: 100%;
        // min-height: 90vh;
        display: flex;
        flex-direction: column;

        .img-container {
            display: none;
            // width: 55%;

            // img {
            //     width: 100%;
            //     height: 100%;
            //     object-fit: cover;
            // }
        }

        .text {
            // flex-grow: 1;
            // width: 100%;
            margin: 35px 15px;
            text-align: center;

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            gap: 50px;

            font-size: 16px;

            .text-block {
                display: flex;
                flex-direction: column;
                gap: 15px;

                p {
                    max-width: 33ch;
                    font-family: os-light;
                }

                span {
                    color: $brand;
                    font-family: os-bold;
                    font-size: 20px;
                    text-shadow: $shadow;
                }
            }

            .btns {
                display: flex;
                flex-direction: column;
                gap: 25px;

                & > * {
                    width: 175px;
                    line-height: 1.35;
                    // padding: 10px 12px;
                }

                .btn-signup {
                    font-size: 18px;
                    color: $white;
                    background-color: $brand;
                    font-family: os-bold;
                }

                .btn-login {
                    font-size: 16px;
                    background: transparent;
                    border: 1px solid $txt;
                    color: $txt;
                }
            }
        }
    }

    .movie-month {
        padding: 25px 0;
        background-color: $brand;

        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            font-family: os-light;
            color: $tool;
            font-size: 16px;
        }

        h3 {
            font-family: os-bold-italic;
            color: $white;
            font-size: 25px;
            text-shadow: $shadow;
        }

        .movie-container {
            background-color: $brand-light;
            padding: 15px 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 15px;
            box-shadow: $shadow;

            margin: 25px 15px;
            border: 2px solid $white;
            width: 300px;

            .movie-month-preview {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: 25px;

                .movie-des {
                    color: $tool;
                    padding: 0 25px;
                    text-align: center;
                    font-size: 14px;
                }

                .btn-watch {
                    background-color: $txt;
                    color: $white;
                    width: 150px;
                    line-height: 1.5;
                    font-size: 16px;
                    border: none;
                    margin-bottom: 20px;
                }
            }

            .img-container {
                width: 85%;
                overflow: hidden;

                img {
                    width: 100%;
                    object-fit: contain;
                }
            }
        }
    }

    .about-us {
        margin: 50px auto 60px;

        .about-us-box {
            width: 300px;
            box-shadow: $shadow;
            background-color: $brand-opc;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 35px;
            padding: 20px;

            h3 {
                color: $brand;
                font-size: 25px;
                font-family: os-light;
            }

            h4 {
                font-size: 18px;
                color: $tool;
                font-family: os-bold;
                max-width: 19ch;
            }

            p {
                font-size: 16px;
                line-height: 1.35;

                span {
                    font-family: os-bold;
                }
            }

            .social-wrapper {
                display: flex;
                justify-content: center;
                gap: 25px;

                svg {
                    fill: $tool;
                    height: 40px;
                }
            }
        }
    }

    .call-to-action {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 50px;
        margin-bottom: 50px;

        h4 {
            text-align: center;
            color: $brand-light;
            font-size: 22px;
            max-width: 20ch;
        }

        .btn-border {
            border-radius: 100px;
            border: 2px solid $brand;
            padding: 1px;
            margin: 0 auto;

            .btn-call {
                // width: 250px;
                line-height: 1.5;
                font-size: 25px;
                border-radius: 100px;
                border: none;
                background-color: $brand;
                color: $white;
                font-weight: 600;
                padding: 7px 25px;
            }
        }
    }
}

@media (min-width: 641px) {
    .home {
        header {
            min-height: 90vh;

            .img-container {
                height: 200px;
                margin-bottom: 100px;
            }

            .headline {
                h1 {
                    font-size: 100px;
                    margin: 0 auto 50px;
                }

                h2 {
                    font-size: 75px;
                    margin: 0 auto 50px;

                    span {
                        letter-spacing: 2px;
                        margin-inline-end: 20px;
                    }
                }
            }
        }

        .info {
            flex-direction: row;

            .img-container {
                display: block;
                width: 55%;

                img {
                    width: 100%;
                    // height: 100%;
                    object-fit: cover;
                }
            }

            .text {
                width: 45%;
                // padding: 0;
                margin: 75px;

                gap: 75px;

                font-size: 30px;

                .text-block {
                    gap: 35px;

                    p {
                        max-width: 40ch;
                    }

                    span {
                        font-size: 40px;
                    }
                }

                .btns {
                    flex-wrap: wrap;
                    justify-content: center;
                    flex-direction: row;
                    gap: 75px;

                    & > * {
                        width: 225px;
                        line-height: 1.75;
                        // padding: 10px 12px;
                    }

                    .btn-signup {
                        font-size: 32px;

                        &:hover {
                            transform: scale(110%);
                        }
                    }

                    .btn-login {
                        font-size: 26px;
                        border: 2px solid $txt;

                        &:hover {
                            background-color: $txt;
                            color: $white;
                        }
                    }
                }
            }
        }

        .movie-month {
            padding: 100px 0;

            h2 {
                font-size: 40px;
                // margin-bottom: 0;
            }

            h3 {
                font-size: 65px;
                // margin-bottom: 50px;
            }

            .movie-container {
                margin: 50px 100px 0;
                border: 3px solid $white;
                padding: 50px;
                flex-direction: row;
                gap: 50px;
                width: auto;

                .movie-month-preview {
                    width: 50%;
                    gap: 50px;

                    .movie-des {
                        padding: 25px;
                        font-size: 24px;
                        max-width: 50ch;
                    }

                    .btn-watch {
                        width: 200px;
                        line-height: 2;
                        font-size: 24px;
                        margin-bottom: 0;
                    }
                }

                .img-container {
                    width: 50%;

                    img {
                        width: 100%;
                        object-fit: contain;
                    }
                }
            }
        }

        .about-us {
            margin: 200px auto 100px;

            .about-us-box {
                gap: 50px;
                padding: 75px 100px;
                width: fit-content;

                h3 {
                    font-size: 50px;
                }

                h4 {
                    font-size: 35px;
                    max-width: 65ch;
                }

                p {
                    font-size: 30px;
                    line-height: 1.5;
                    max-width: 65ch;
                    // padding: 25px;
                }

                .social-wrapper {
                    gap: 25px;

                    svg {
                        cursor: pointer;
                        height: 75px;
                    }
                }
            }
        }

        .call-to-action {
            gap: 50px;
            // padding: 100px;
            margin-bottom: 100px;

            h4 {
                font-size: 40px;
                max-width: none;
            }

            .btn-border {
                border: 3px solid $brand;
                padding: 1px;
                margin: 0 auto;

                .btn-call {
                    // width: 250px;
                    line-height: 2;
                    font-size: 40px;
                    font-weight: 600;
                    padding: 10px 50px;
                }
            }
        }
    }
}
