.movie-details {
    flex-grow: 1;
    direction: rtl;
    background-color: $bg;

    padding: 25px;
    overflow-y: auto;

    display: flex;
    flex-direction: column-reverse;
    gap: 25px;

    .movie-container {
        .img-container {
            img {
                width: 100%;
                aspect-ratio: 16/9;
                object-fit: cover;
            }
        }
    }

    .info {
        display: flex;
        flex-direction: column;

        h2.title {
            color: $brand;
            font-size: 22px;

            margin-bottom: 4px;
        }

        .catagories {
            display: flex;
            flex-wrap: wrap;
            gap: 5px;

            margin: 15px 0;

            & > * {
                color: $white;
                background-color: $txt;
                border-radius: 100px;

                font-size: 14px;
                padding: 5px 10px;
            }
        }

        .des {
            display: flex;
            flex-direction: column;
            gap: 7px;

            p.full-des {
                font-size: 16px;
                line-height: 1.35;
            }

            .credits {
                margin-bottom: 5px;
                p.credit {
                    font-size: 14px;
                }
            }
        }

        .extra-details {
            font-size: 14px;
        }

        .btns-container {
            display: flex;
            justify-content: center;
            gap: 25px;

            margin-top: 50px;

            button {
                font-size: 16px;
                padding: 10px 15px;
            }

            .watch {
                color: $white;
                background-color: $brand;
                border: none;
            }

            .back {
                color: $brand;
                background: transparent;
                border: 1px solid $brand;
            }
        }
    }
}

@media (min-width: 641px) {
    .movie-details {
        direction: rtl;
        height: calc(100vh - 135px);
        width: 100vw;

        display: flex;
        flex-direction: row;
        gap: 150px;
        
        background-color: $bg;
        padding: 100px 200px;

        .info {
            flex-basis: 50%;

            height: 100%;

            display: flex;
            flex-direction: column;

            h2.title {
                color: $brand;

                font-size: 25px;
                margin-bottom: 7px;
            }

            span.length {
                display: block;
                font-weight: 300;
            }

            .catagories {
                gap: 10px;

                margin: 35px 0;

                & > * {
                    color: $white;
                    background-color: $txt;
                    border-radius: 100px;
                    
                    font-size: 16px;
                    padding: 5px 10px;
                }
            }

            .des {
                display: flex;
                flex-direction: column;

                p.full-des {
                    font-size: 17px;
                    line-height: 1.35;
                }

            }

            .btns-container {
                display: flex;
                justify-content: flex-start;
                gap: 25px;

                margin-top: 50px;

                button {
                    font-size: 16px;
                    padding: 10px 15px;
                }

                .watch {
                    border: none;
                    background-color: $brand;
                    color: $white;
                }

                .back {
                    background: transparent;
                    border: 1px solid $brand;
                    color: $brand;
                }
            }
        }

        .movie-container {
            flex-basis: 50%;
            position: relative;
            overflow: hidden;
            align-self: flex-start;
            height: fit-content;

            .img-container {
                img {
                    width: 100%;
                    aspect-ratio: 16/9;
                    object-fit: cover;
                }
            }
        }
    }
}
