.movies-main {
    flex-grow: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    direction: ltr;

    .movie-list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 40px;

        margin-bottom: 50px;


        button.scroll {
            color: $brand;
            background: transparent;
            border: 1px solid $txt;
            font-size: 16px;
            padding: 5px 10px;
        }
    }
}

@media (min-width: 641px) {
    .movies-main {
        height: calc(100vh - 135px);

        display: flex;
        flex-direction: column;
        align-items: center;

        position: relative;
        overflow-y: auto;

        .movie-list {
            width: 1000px;
            
            display: flex;
            flex-direction: row-reverse;
            flex-wrap: wrap;
            gap: 50px 25px;

            margin-bottom: 50px;
            

            button.scroll {
                display: none;
            }
        }
    }
}
